import { useCallback } from 'react'
import { useSelector, shallowEqual } from 'react-redux'

export const mapDispatchToUseCallback = (dispatch, actions) =>
  Object.entries(actions).reduce((accumulator, [k, v]) => {
    accumulator[k] = useCallback(() => dispatch(v), [dispatch])
    return accumulator
  }, {})

export const useShallowSelector = selector =>
  useSelector(selector, shallowEqual)

export const getPromiseRef = () => {
  const ref = {}
  const promise = new Promise((resolve, reject) => {
    ref.resolve = resolve
    ref.reject = reject
  })
  return { ...ref, promise }
}
