import cx from 'classnames'
import React, { useState } from 'react'
import { useEffectOnce } from 'react-use'
import { CircularProgress } from '@material-ui/core'

import { getPromiseRef } from '../utils'

import './photo.css'

const LOAD_PHOTO_DELAY = 1000

export const Photo = ({ photo, photos, setPhotos, onClick }) => {
  const [isLoaded, setIsLoaded] = useState(false)
  let isMounted = false

  useEffectOnce(() => {
    isMounted = true

    async function loadPhoto() {
      const image = new Image()
      const wait = new Promise(resolve => setTimeout(resolve, LOAD_PHOTO_DELAY))
      const { resolve, promise: onLoad } = getPromiseRef()
      image.onload = () => resolve()
      image.src = photo.thumbnailUrl
      await Promise.all([wait, onLoad])
      isMounted && setIsLoaded(true)
    }

    loadPhoto()

    return () => (isMounted = false)
  })

  return (
    <div
      title={photo.title}
      className="Photo grid-item"
      onClick={() => onClick(photo.id)}
    >
      <div
        style={{ backgroundImage: `url(${photo.thumbnailUrl})` }}
        className={cx('Photo-image', { 'Photo-image-is-loaded': isLoaded })}
      ></div>
      {isLoaded ? (
        <React.Fragment>
          <div className="Photo-title line-clamp">{photo.title}</div>
          <div
            className="Photo-dismiss"
            onClick={event => {
              event.stopPropagation()
              const otherPhotos = _photo => _photo.id !== photo.id
              setPhotos(photos.filter(otherPhotos))
            }}
          />
        </React.Fragment>
      ) : (
        <CircularProgress color="inherit" size={20} />
      )}
    </div>
  )
}

export default React.memo(Photo)
